/**
 * ArticleCard 图文卡片
 * @description 卡片视图组件
 * @tutorial https://ext.dcloud.net.cn/plugin?id=22
 * @property {String} title 文章标题
 * @property {String} author 文章作者名称
 * @property {String, Number} number 文章评论数
 * @property {String, Number} publishTime 文章发布时间
 * @property {Array} imageList 三图图片列表
 * @property {String} path 图片路径 
 * @property {String} mode = [basic|single|triple|large] 卡片模式
 * 	@value basic 基础卡片
 * 	@value single 单图卡片
 * 	@value triple 三图卡片
 *  @value large 大图卡片
 * @event {Function} click 点击 Card 触发事件
 * @example <uni-card title="标题文字" thumbnail="https://img-cdn-qiniu.dcloud.net.cn/new-page/uni.png" extra="额外信息" note="Tips">内容主体，可自定义内容及样式</uni-card>
 */
export default {
	name: 'ArticleCard',
	props: {
		url: {
			type: String,
			default: ''
		},//跳转链接
		title: {
			type: String,
			default: ''
		}, // 标题
		author: {
			type: String,
			default: ''
		}, // 作者
		comment: {
			type: [String, Number],
			default: '0'
		}, // 评论数
		publishTime: {
			type: [String, Number],
			default: '1小时前'
		}, // 发布文章的时间
		imageList: {
			type: Array,
			default () {
				return []
			}
		}, //三图图片数组
		// 卡片模式 ， 可选值 basic 基础卡片 ；single 单图卡片 ； triple 三图卡片 ；large 大图卡片
		mode: {
			type: String,
			default: 'basic'
		},
		path: {
			type: String,
			default: ''
		}
	},
	methods: {
		onClick() {
			this.$emit('click')
		}
	}
}
